<template>
  <article class="article">
    <template v-if="errorsLength <= 0">

      <h2 v-if="companies.length" class="header form--header header__no-underline">Ми підібрали {{companies.length}} пропозиц{{offerEnding}} для Вас</h2>

      <template v-else>
        <h2 class="header form--header header__no-underline">
          На жаль, не вдалося автоматично знайти пропозиції, які б відповідали обраним параметрам.
        </h2>
        <p class="header form--header header__no-underline">
          Будь ласка, зверніться до нашого представника за номером телефону : +38099788455</p>
      </template>

      <div class="companies--wrapper">
        <table class="companies">
          <tr class="companies--tr companies--tr__header">
            <th class="companies--th hidden-mob">№</th>
            <th class="companies--th companies--th__static-mob">
              <span class="companies--header-text">Компанія</span>
              <button class="companies--sort" @click="sort('name', [1, -1])">
                <span class="visually-hidden">За зменшенням</span>
                <svg class="sort--icon" aria-hidden="true" viewBox="0 0 292.362 292.362">
                  <use xlink:href="/Content/img/icons.svg#sort"></use>
                </svg>
              </button
              >
              <button class="companies--sort companies--sort__descending" @click="sort('name', [-1, 1])">
                <span class="visually-hidden">За збільшенням</span>
                <svg class="sort--icon" aria-hidden="true" viewBox="0 0 292.362 292.362">
                  <use xlink:href="/Content/img/icons.svg#sort"></use>
                </svg>
              </button>
            </th>
            <th class="companies--th">
              <span class="companies--header-text">Рейтинг</span>
              <button class="companies--sort" @click="sort('rate', [1, -1])">
                <span class="visually-hidden">За зменшенням</span>
                <svg class="sort--icon" aria-hidden="true" viewBox="0 0 292.362 292.362">
                  <use xlink:href="/Content/img/icons.svg#sort"></use>
                </svg>
              </button
              >
              <button class="companies--sort companies--sort__descending" @click="sort('rate', [-1, 1])">
                <span class="visually-hidden">За збільшенням</span>
                <svg class="sort--icon" aria-hidden="true" viewBox="0 0 292.362 292.362">
                  <use xlink:href="/Content/img/icons.svg#sort"></use>
                </svg>
              </button>
            </th>
            <th class="companies--th">
              <span class="companies--header-text">Переваги</span>
            </th>
            <th class="companies--th">
              <span class="companies--header-text">Франшиза</span>
              <button class="companies--sort" @click="sort('frans', [1, -1])">
                <span class="visually-hidden">За зменшенням</span>
                <svg class="sort--icon" aria-hidden="true" viewBox="0 0 292.362 292.362">
                  <use xlink:href="/Content/img/icons.svg#sort"></use>
                </svg>
              </button
              >
              <button class="companies--sort companies--sort__descending" @click="sort('frans', [-1, 1])">
                <span class="visually-hidden">За збільшенням</span>
                <svg class="sort--icon" aria-hidden="true" viewBox="0 0 292.362 292.362">
                  <use xlink:href="/Content/img/icons.svg#sort"></use>
                </svg>
              </button>
            </th>
            <th class="companies--th">
              <span class="companies--header-text">Загальна ціна</span>
              <button class="companies--sort" @click="sort('newPrice', [1, -1])">
                <span class="visually-hidden">За зменшенням</span>
                <svg class="sort--icon" aria-hidden="true" viewBox="0 0 292.362 292.362">
                  <use xlink:href="/Content/img/icons.svg#sort"></use>
                </svg>
              </button
              >
              <button class="companies--sort companies--sort__descending" @click="sort('newPrice', [-1, 1])">
                <span class="visually-hidden">За збільшенням</span>
                <svg class="sort--icon" aria-hidden="true" viewBox="0 0 292.362 292.362">
                  <use xlink:href="/Content/img/icons.svg#sort"></use>
                </svg>
              </button>
            </th>
            <th class="companies--th"><span class="visually-hidden">Обрати</span></th>
          </tr>

          <template v-for="(company, index) in companies">
            <tr class="companies--tr">
              <td class="companies--td hidden-mob">{{index + 1}}</td>
              <td class="companies--td companies--td__static-mob">
                <a href="company.PageCompanyInfoPath" class="companies--link">
                  <img class="companies--logo"
                       :class="imageClass(company.ImageSize)"
                       :src="company.ImgPath"
                       :alt="company.CompanyName">
                  <span class="companies--name">{{company.CompanyName}}</span>
                </a>
              </td>
              <td class="companies--td compcompanies--rate companies--td__rate"
                  :class="rateClass(company.CompanyRate)">
                {{company.CompanyRate}}
              </td>
              <td class="companies--td companies--td__features">
                <ul>
                  <li v-for="feature in company.CompanyFeatures"
                      class="companies--feature">
                    {{feature}}
                  </li>
                </ul>
              </td>
              <td class="companies--td companies--td__frans">{{company.Franchise}}</td>
              <td
                class="companies--td companies--td__price">
                <div class="companies--wrap">
							<span class="companies--price"
                    v-if="company.FullPrice !== company.DiscountPrice"
                    :class="company.FullPrice === company.DiscountPrice ? '' : 'companies--price__strike'">
								  {{company.FullPrice}}</span>
                  <b class="companies--actual-price">
                    <span class="companies--price">{{company.DiscountPrice}}</span>
                    <span class="companies--currency">грн</span>
                  </b>
                </div>
              </td>
              <td class="companies--td">
                <button @click.prevent="$emit('companySelection', getCompanyInfo($event.target))"
                        class="companies--choose">Обрати
                </button>
              </td>
            </tr>
          </template>
        </table>
      </div>

      <selected-conditions></selected-conditions>
    </template>

    <template v-else>
      <h2 class="header form--header header__no-underline">
        Помилка з обраними даними. <br> Поверніться на попередню сторінку та заповніть всі поля.
      </h2>

      <div>
        <router-link class="router--link" to="/">Змінити дані</router-link>
      </div>
    </template>
  </article>
</template>

<script>
  import {mapGetters} from "vuex";
  import {mapActions} from "vuex";
  import SelectedConditions from "./SelectedConditions";

  export default {
    props: ["companies"],

    data() {
      return {
        /*companies: [
          {
            name: "ТАС",
            rate: 71,
            features: ["Любимчик МТСБУ", "Красавчик", "Лучший офис в галактике"],
            frans: 0,
            oldPrice: 1020,
            newPrice: 1020,
            linkToImage: "/Content/img/partners/TAS.png",
            imageSize: 2,
            linkToCompInfo: ""
          },
          {
            name: "ТАС",
            rate: 71,
            features: ["Любимчик МТСБУ", "Красавчик", "Лучший офис в галактике"],
            frans: 500,
            oldPrice: 1250,
            newPrice: 1250,
            linkToImage: "/Content/img/partners/TAS.png",
            imageSize: 2,
            linkToCompInfo: ""
          },
          {
            name: "ТАС",
            rate: 71,
            features: ["Любимчик МТСБУ", "Красавчик", "Лучший офис в галактике"],
            frans: 1000,
            oldPrice: 1590,
            newPrice: 1590,
            linkToImage: "/Content/img/partners/TAS.png",
            imageSize: 2,
            linkToCompInfo: ""
          },
          {
            name: "AXA",
            rate: 100,
            features: ["Любимчик МТСБУ", "Бог дискотеки", "Победитель по жизни"],
            frans: 0,
            oldPrice: 1400,
            newPrice: 1200,
            linkToImage: "/Content/img/partners/AXA.png",
            imageSize: 0,
            linkToCompInfo: ""
          },
          {
            name: "Оранта",
            rate: 54,
            features: ["Богач", "Успешный успех", "Сын маминой подруги"],
            frans: 1000,
            oldPrice: 1100,
            newPrice: 1100,
            linkToImage: "/Content/img/partners/ORANTA.png",
            imageSize: 2,
            linkToCompInfo: ""
          },
          {
            name: "Украинская Страховая Группа",
            rate: 31,
            features: ["От души", "Делает красиво"],
            frans: 0,
            oldPrice: 1300,
            newPrice: 1000,
            linkToImage: "/Content/img/partners/ukr-ins-group.png",
            imageSize: 1,
            linkToCompInfo: ""
          },
        ]*/
      }
    },
    computed: {
      ...mapGetters("transport", {
        "transExtraName": "transExtraName"
      }),
      ...mapGetters("errors", {
        "errors": "errors",
        "errorsLength": "errorsLength"
      }),
      ...mapGetters([
        "transportData"
      ]),
      offerEnding() {
        let val = this.companies.length % 100;

        if (val > 10 && val < 20) return "ій";
        else {
          val = this.companies.length % 10;
          if (val === 1) return "ію";
          else if (val > 1 && val < 5) return "ії";
          else return "ій";
        }

        // Русские окончания
        /*if (val > 10 && val < 20) return "ий";
        else {
          val = this.companies.length % 10;
          if (val === 1) return "ие";
          else if (val > 1 && val < 5) return "ия";
          else return "ий";
        }*/
      }
    },

    methods: {
      imageClass(imageSize) {
        if (imageSize) {
          if (imageSize === 1) {
            return "companies--logo__long"
          }
          if (imageSize === 2) {
            return "companies--logo__very-long"
          }
        }
        else {
          return ""
        }
      },
      rateClass(rate) {
        switch (true) {
          case (rate >= 1 && rate <= 20):
            return "companies--rate__bad";
            break;
          case (rate >= 21 && rate <= 40):
            return "companies--rate__soso";
            break;
          case (rate >= 41 && rate <= 60):
            return "companies--rate__norm";
            break;
          case (rate >= 61 && rate <= 80):
            return "companies--rate__nice";
            break;
          case (rate >= 81 && rate <= 100):
            return "companies--rate__good";
            break;
        }
      },
      sort(param, pointersArr) {
        function compare(a, b) {
          if (a[param] < b[param])
            return pointersArr[0];
          if (a[param] > b[param])
            return pointersArr[1];
          return 0;
        }

        this.companies.sort(compare);
      },
      getCompanyInfo(target) {
        const tr = target.closest(".companies--tr");
        const companyName = tr.querySelector(".companies--name").textContent;
        const companyFrans = tr.querySelector(".companies--td__frans").textContent;
        const companyPrice = tr.querySelector(".companies--actual-price").textContent;

        return {
          company: companyName,
          frans: companyFrans,
          price: companyPrice
        }
      }
    },

    mounted() {
      const sortBtns = document.querySelectorAll(".companies--sort");
      for (let i = 0; i < sortBtns.length; i++) {
        sortBtns[i].addEventListener("click", function () {
          for (let j = 0; j < sortBtns.length; j++) {
            sortBtns[j].classList.remove("companies--sort__active")
          }
          this.classList.add("companies--sort__active");
        })
      }
    },

    components: {
      SelectedConditions
    }

  }
</script>


<style lang="less">
  .companies {
    border-collapse: separate;
    margin-bottom: 20px;
    width: 100%;

    &--wrapper {
      width: 90%;
      margin: 0 auto;
      @media only screen and (max-width: 1195px) {
        width: 100%;
      }
      @media only screen and (max-width: 576px) {
        width: auto;
        overflow-x: scroll;
        margin-left: 100px;
        overflow-y: visible;
        margin-bottom: 40px;
      }
    }

    &--tr {
      position: relative;
      transition: box-shadow 0.1s;
      &:hover {
        box-shadow: 1px 1px 5px #DDDDDD;
        @media only screen and (max-width: 576px) {
          box-shadow: none;
        }
      }
      &__header {
        &:hover {
          box-shadow: none;
        }
      }
    }

    &--th {
      box-sizing: border-box;
      vertical-align: middle;
      font-weight: bold;
      border-bottom: 2px solid #F3F4F6;
      padding-bottom: 10px;
      font-size: 22px;
      @media only screen and (max-width: 1195px) {
        font-size: 18px;
      }
      @media only screen and (max-width: 768px) {
        font-size: 16px;
        white-space: nowrap;
        padding-left: 4px;
        padding-right: 4px;
      }
      &__static-mob {
        @media only screen and (max-width: 576px) {
          position: absolute;
          width: 100px;
          left: 5px;
          top: auto;
          padding: 0;
          padding-bottom: 10px;
        }
      }
    }

    &--header-text {
      @media only screen and (max-width: 1195px) {
        display: block;
      }
    }

    &--td {
      padding: 10px 0;
      min-height: 100px;
      line-height: 1;
      vertical-align: middle;
      border-bottom: 2px solid #F3F4F6;
      @media only screen and (max-width: 576px) {
        min-height: auto;
        height: 85px;
        box-sizing: border-box;

      }
      &__rate {
        font-size: 25px;
        @media only screen and (max-width: 1195px) {
          font-size: 22px;
        }
        @media only screen and (max-width: 768px) {
          font-size: 18px;
        }
      }
      &__features {
        font-size: 17px;
        @media only screen and (max-width: 1195px) {
          font-size: 16px;
        }
        @media only screen and (max-width: 768px) {
          font-size: 15px;
        }
      }
      &__frans {
        font-size: 25px;
        @media only screen and (max-width: 1195px) {
          font-size: 22px;
        }
        @media only screen and (max-width: 768px) {
          font-size: 18px;
        }
      }
      &__no-line {
        border-bottom: none;
      }
      &__static-mob {
        @media only screen and (max-width: 576px) {
          position: absolute;
          width: 100px;
          left: 5px;
          top: auto;
          padding: 0;
          display: flex;
          align-items: center;
        }
      }
    }

    &--wrap {
      display: inline-block;
      position: relative;
    }

    &--link {
      vertical-align: middle;
      margin: 0 auto;
    }

    &--logo {
      display: inline-block;
      height: 50px;
      width: auto;
      border: none;
      @media only screen and (max-width: 1195px) {
        height: 47px;
      }
      @media only screen and (max-width: 768px) {
        height: 40px;
      }

      &__long {
        height: 45px;
        @media only screen and (max-width: 1195px) {
          height: 42px;
        }
        @media only screen and (max-width: 768px) {
          height: 35px;
        }
      }
      &__very-long {
        height: 35px;
        @media only screen and (max-width: 1195px) {
          height: 32px;
        }
        @media only screen and (max-width: 768px) {
          height: 25px;
        }
      }
    }

    &--name {
      display: block;
      color: gray;
      font-size: 15px;
      line-height: 1;
      @media only screen and (max-width: 1195px) {
        font-size: 14px;
      }
      @media only screen and (max-width: 576px) {
        display: none;
      }
    }

    &--rate {
      &__good {
        color: #32c858;
      }
      &__nice {
        color: #7fc231;
      }
      &__norm {
        color: #FFD94C;
      }
      &__soso {
        color: #FFB248;
      }
      &__bad {
        color: #FF8C5D;
      }
    }

    &--feature {
      margin-bottom: 7px;
      @media only screen and (max-width: 768px) {
        margin-bottom: 5px;
      }
    }

    &--price {
      color: #32c858;
      font-size: 35px;
      @media only screen and (max-width: 1195px) {
        font-size: 30px;
      }
      @media only screen and (max-width: 768px) {
        font-size: 25px;
      }
      &__strike {
        position: absolute;
        top: -17px;
        right: 0;
        display: inline-block;
        text-decoration: line-through;
        font-size: 20px;
        color: #FF8C5D;
        @media only screen and (max-width: 1195px) {
          font-size: 18px;
        }
        @media only screen and (max-width: 768px) {
          font-size: 16px;
          top: -12px;
        }
      }
    }

    &--currency {
      color: #32c858;
      margin-left: -3px;
      display: inline-block;
      font-size: 20px;
      @media only screen and (max-width: 1195px) {
        font-size: 18px;
      }
      @media only screen and (max-width: 768px) {
        font-size: 16px;
      }
    }

    &--choose {
      box-shadow: 2px 5px 4px 0px #DDDDDD;
      background-color: #0093e8;
      color: white;
      padding: 10px 15px;
      border-radius: 5px;
      font: inherit;
      font-size: 18px;
      @media only screen and (max-width: 768px) {
        font-size: 16px;
        padding: 8px 10px;
      }
      @media only screen and (max-width: 576px) {
        margin-right: 10px;
      }
    }

    &--sort {
      display: inline-block;
      vertical-align: middle;
      width: 14px;
      height: 14px;
      margin-right: 2px;
      &__descending {
        transform: rotate(-180deg);
      }
      &:hover,
      &__active {
        .sort--icon {
          fill: #0093e8;
          transition: all 0.2s;
        }
      }
    }
  }
</style>

<style lang="less">
  .hidden-mob {
    @media only screen and (max-width: 768px) {
      display: none;
    }
  }
</style>



